.menu {
  background-color: $colorRed;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;

    @include onTablet() {
      padding-top: $headerTopPadding + 25px;
    }
  }

  &__top {
    @include top-actions;
    margin-bottom: 40px;

    @include onDesktop() {
      margin-bottom: 55px;
    }
  }

  &__list {
    margin: 0 0 32px;
    padding: 0;
    list-style: none;

    @include onDesktop() {
      margin-bottom: 48px;
    }
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;

    transition: border-color 0.3s;

    &:hover {
      border-color: #fff;
    }
  }

  &__contacts {
    display: block;
    width: max-content;
    margin-bottom: 8px;

    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;

    &--adress {
      margin-bottom: 0;
    }

    &--action {
      margin-top: 16px;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      padding-bottom: 8px;
      border-bottom: 1px solid #fff;
    }
  }
}
